.address-block__wordmark {
  margin-bottom: 0.5em;

  img {
    width: 280px;
    border: none;
  }
}
.address-block__address {
  p {
    margin: 0;
    font-size: 0.875rem;
    line-height: 1.375;
    font-weight: 300;
  }

  .site-name {
    font-weight: 600;
    font-size: 1rem;
    margin-bottom: 2px;
  }

  .pipe {
    font-size: 120%;
    vertical-align: -2%;
  }

  span.mailto {
    display: none;
  }
}
